@import url(./vendor/swiper.min.css);
@import url(../../node_modules/simplebar/dist/simplebar.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #1A1917    ;
  --quill-gray: #e5e5e3;
  --color-red: #FF1313;
  --color-orange: #FFB548;
}

@font-face {
  font-family: "bebas-neue";
  src: url("../fonts/bebas-neue-bold.woff2") format("woff2"), url("../bebas-neue-bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

