html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "OpenSans", sans-serif;
  font-weight: 700;
  color: var(--color-white);
  background-color: var(--color-dark);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  margin: 0 auto;
  position: relative;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 120px;
  width: 100%;
  height: 100%;
}

@media (max-width: 1025px) {
  .container {
    padding: 0 50px;
  }
}

@media (max-width: 576px) {
  .container {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

section {
  height: 100vh;
  max-height: 960px;
  min-height: 800px;
}

@media (max-width: 1025px) {
  section {
    min-height: 700px;
  }
}

@media (max-width: 768px) {
  section {
    min-height: 600px;
  }
}

.question {
  padding: 0;
  margin: 0;
  font-family: "bebas-neue";
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 2px;
}

@media (max-width: 768px) {
  .question {
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 2px;
  }
}

.title {
  padding: 0;
  margin: 0;
  font-family: "bebas-neue";
  font-weight: bold;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: 3px;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .title {
    font-size: 33px;
    line-height: 33px;
    letter-spacing: 3px;
  }
}

.btn-link {
  padding: 17px;
  background: linear-gradient(194.68deg, #FFB548 11.27%, #F3AF34 90.4%);
  border-radius: 4px;
  position: relative;
}

@media (max-width: 768px) {
  .btn-link {
    padding: 12px;
    font-size: 14px;
    line-height: 19px;
  }
}

.btn-link a {
  font-size: 16px;
  line-height: 22px;
  color: var(--color-dark);
}

@media (max-width: 768px) {
  .btn-link a {
    font-size: 14px;
    line-height: 19px;
  }
}

.btn-link a::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

/* companents */
.header {
  position: relative;
  padding-top: 23px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 2;
}

.header__right {
  display: flex;
  width: 170px;
  justify-content: space-between;
  align-items: center;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.select {
  font-size: 12px;
  line-height: 16px;
  text-align: right;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  position: relative;
}

.select::after {
  content: "";
  position: absolute;
  display: block;
  width: 2px;
  height: 40px;
  top: 50%;
  transform: translateY(-50%);
  right: -30px;
  background-color: var(--color-white);
}

@media (max-width: 1025px) {
  .select::after {
    right: -10px;
  }
}

.marcet {
  display: flex;
  align-items: center;
}

.marcet__link {
  width: 30px;
  height: 30px;
}

.marcet__link:not(:last-child) {
  margin-right: 16px;
}

.marcet__link svg {
  width: inherit;
  height: inherit;
}

.burger {
  width: 25px;
  height: 30px;
  display: none;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 999;
  position: relative;
}

.burger::after {
  content: '';
  display: block;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 22;
}

.burger__item {
  display: block;
  height: 2px;
  width: 25px;
  background-color: var(--color-white);
  position: relative;
}

.burger__item::after, .burger__item::before {
  content: '';
  display: block;
  height: 2px;
  width: 100%;
  position: absolute;
  background-color: inherit;
}

.burger__item::before {
  top: -10px;
}

.burger__item::after {
  top: 10px;
  width: 50%;
  right: 0;
}

@media (max-width: 1025px) {
  .burger {
    display: flex;
  }
}

.nav {
  width: 610px;
}

.nav__list {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 768px) {
  .nav__item:not(:last-child) {
    margin-bottom: 10px;
  }
}

.nav__link {
  text-transform: uppercase;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.1em;
}

@media (max-width: 1025px) {
  .nav {
    width: 100%;
    position: absolute;
    right: 0;
    top: 0;
  }
  .nav__list {
    width: 360px;
    position: absolute;
    right: 50px;
    top: -100vh;
    flex-direction: column;
    align-items: flex-end;
    transition: all ease .4s;
    z-index: 50;
  }
}

@media (max-width: 1025px) and (max-width: 576px) {
  .nav__list {
    left: 0;
    width: 80%;
  }
  .nav__list::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100vh;
    left: 0;
    top: -150vh;
    background-color: #16160dc9;
    z-index: -1;
  }
}

@media (max-width: 1025px) {
  .nav__list-active {
    top: 100px;
    padding-right: 30px;
  }
}

@media (max-width: 1025px) and (max-width: 576px) {
  .nav__list-active {
    top: 20px;
    left: 0;
  }
  .nav__list-active::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 150vh;
    left: 0;
    top: -20px;
    background-color: #16160dc9;
    z-index: -1;
  }
}

.top {
  background-image: url(../img/top-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.top__wrapper {
  width: 100%;
  height: 100vh;
  min-height: 800px;
  max-height: 960px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

@media (max-width: 1025px) {
  .top__wrapper {
    min-height: 700px;
  }
}

@media (max-width: 768px) {
  .top__wrapper {
    min-height: 600px;
  }
}

.top__title {
  margin-bottom: 10px;
  font-family: "bebas-neue";
  font-weight: bold;
  font-size: 80px;
  line-height: 80px;
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .top__title {
    font-size: 33px;
    line-height: 33px;
    text-align: center;
    letter-spacing: 3px;
  }
}

.top__desc {
  margin: 0;
  margin-bottom: 35px;
  font-family: "bebas-neue";
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 2px;
}

@media (max-width: 768px) {
  .top__desc {
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 2px;
  }
}

.top__buy {
  margin-bottom: calc(100vh/2.5);
  padding: 17px;
  background: linear-gradient(194.68deg, #FFB548 11.27%, #F3AF34 90.4%);
  border-radius: 4px;
  position: relative;
}

@media (max-width: 1025px) {
  .top__buy {
    margin-bottom: calc(100vh/4);
  }
}

@media (max-width: 768px) {
  .top__buy {
    padding: 12px;
    margin-bottom: calc(100vh/3);
  }
}

.top__link {
  font-size: 16px;
  line-height: 22px;
  color: var(--color-dark);
}

.top__link::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

@media (max-width: 768px) {
  .top__link {
    font-size: 14px;
    line-height: 19px;
  }
}

.top__price {
  display: inline-block;
  position: relative;
  padding-left: 20px;
  font-size: 16px;
  line-height: 22px;
  color: var(--color-dark);
}

.top__price::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 25px;
  background-color: var(--color-dark);
  left: 7px;
  top: 50%;
  transform: translateY(-50%) rotate(-3deg);
}

@media (max-width: 768px) {
  .top__price {
    font-size: 14px;
    line-height: 19px;
  }
}

.top__the-story {
  font-size: 12px;
  line-height: 22px;
  text-transform: uppercase;
  position: relative;
  margin-bottom: 40px;
}

.top__the-story svg {
  position: absolute;
  width: 30px;
  height: 12px;
  left: 50%;
  bottom: -12px;
  transform: translateX(-50%);
}

.swiper-top {
  overflow: hidden;
}

.social {
  background: url(../img/social-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /*    slider   */
}

@media (max-width: 1025px) {
  .social {
    height: auto;
    padding-top: 30px;
  }
}

.social__container {
  display: flex;
  padding: 0  100px;
}

@media (max-width: 1025px) {
  .social__container {
    width: 100%;
    flex-direction: column;
    align-items: center;
    padding: 0 40px;
  }
}

@media (max-width: 576px) {
  .social__container {
    padding: 0 15px;
  }
}

.social__column {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social__column-1 {
  width: 100%;
  max-width: 700px;
  transform: translateY(30px);
  position: relative;
  z-index: 3;
}

@media (max-width: 1025px) {
  .social__column-1 {
    transform: translate(0);
  }
}

.social__column-2 {
  width: 550px;
  transform: translate(30px, 60px);
}

@media (max-width: 1025px) {
  .social__column-2 {
    transform: translate(0);
  }
}

@media (max-width: 768px) {
  .social__column-2 {
    width: 300px;
  }
}

.social__title {
  margin-bottom: 70px;
}

@media (max-width: 768px) {
  .social__title {
    max-width: 400px;
    margin-bottom: 30px;
  }
}

.social__text {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
}

.social__text p {
  display: inline-block;
  margin: 10px 0;
  width: 360px;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: justify;
}

@media (max-width: 768px) {
  .social__text p {
    font-size: 14px;
    line-height: 16px;
  }
}

@media (max-width: 576px) {
  .social__text p {
    max-width: 360px;
    width: 100%;
    padding-left: 10px;
  }
}

.social__text span {
  position: relative;
}

.social__text span::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 6px;
  left: 0;
  bottom: 2px;
  opacity: .8;
  background-color: #FFB548;
}

.social__text::after {
  content: '';
  display: block;
  position: absolute;
  width: 130%;
  height: 3px;
  background-color: var(--color-white);
  left: 0;
  top: -50px;
  z-index: 100;
}

@media (max-width: 1025px) {
  .social__text::after {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .social__text::after {
    top: -10px;
  }
}

.social__swiper {
  overflow: hidden;
}

.social__swiper-next {
  position: absolute;
  right: 0;
  top: 50%;
  width: 36px;
  height: 20px;
  transform: rotate(-90deg);
}

.social__swiper-next svg {
  width: inherit;
  height: inherit;
}

.social__swiper-pagination {
  display: flex;
  justify-content: center;
  transform: translateY(-40px);
  position: relative;
  z-index: 22;
}

.social__swiper-pagination .swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  border: 2px solid var(--color-white);
  background-color: inherit;
  transition: all ease .3s;
}

@media (max-width: 768px) {
  .social__swiper-pagination .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
  }
}

.social__swiper-pagination .swiper-pagination-bullet::after {
  content: "";
  position: absolute;
  width: 0px;
  height: 1px;
  background-color: var(--color-white);
  top: 50%;
  left: 12px;
  transition: all ease 0s;
}

@media (max-width: 768px) {
  .social__swiper-pagination .swiper-pagination-bullet::after {
    left: 0px;
  }
}

.social__swiper-pagination .swiper-pagination-bullet-active {
  background-color: var(--color-white);
  position: relative;
}

.social__swiper-pagination .swiper-pagination-bullet-active::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 1px;
  background-color: var(--color-white);
  top: 50%;
  transition: all ease .3s;
}

@media (max-width: 768px) {
  .social__swiper-pagination {
    transform: translateY(-20px);
  }
}

.social__img {
  height: 600px;
  border-radius: 5px;
