.accard__item {
  margin-top: -10px;
  padding-bottom: 20px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

@media (max-width: 768px) {
  .accard__item {
    margin-top: -4px;
  }
}

.accard__item::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  border: 1px solid var(--color-white);
  border-radius: 50%;
  left: 0;
  top: 10px;
  transition: all linear .3s;
}

@media (max-width: 768px) {
  .accard__item::before {
    left: 15px;
    top: 3px;
  }
}

.accard__item::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 0px;
  border: 1px solid var(--color-white);
  left: 6px;
  top: 23px;
  opacity: 0;
  transition: all linear .4s;
}

@media (max-width: 768px) {
  .accard__item::after {
    left: 21px;
    top: 16px;
  }
}

.accard__item-active::before {
  background-color: var(--color-white);
}

.accard__item-active:not(:last-child)::after {
  opacity: 1;
  height: 311px;
}

.accard__item-active .accard__text {
  opacity: 1;
  max-height: 1000px;
}

